body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
}

.snake-game {
  text-align: center;
}

.game-board {
  border: 2px solid #333;
  background-color: #fff;
  margin: 0 auto;
  touch-action: none; /* Prevents default touch behaviors like scrolling */
  user-select: none; /* Prevents text selection during swipes */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.grid-cell {
  width: 20px;
  height: 20px;
  border: 1px solid #eee;
}

.snake {
  background-color: green;
}

.food {
  background-color: red;
}

.score {
  font-size: 24px;
  margin-bottom: 10px;
}

.game-over {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-over button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.game-over button:hover {
  background-color: #45a049;
}

/* Responsive grid sizing */
@media (max-width: 600px) {
  .game-board {
    transform: scale(0.8);
    transform-origin: top center;
  }
  
  .snake-game h1 {
    font-size: 24px;
    margin: 10px 0;
  }
  
  .score {
    font-size: 20px;
    margin: 5px 0;
  }
  
  .game-over h2 {
    font-size: 22px;
  }
  
  .game-over button {
    font-size: 16px;
    padding: 8px 16px;
  }
}

@media (max-width: 400px) {
  .game-board {
    transform: scale(0.6);
  }
}
